
* {
  outline: 0 !important;
}

::-moz-focus-outer,
::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.button {
  position: relative;
  display: inline-block;
  padding: 0 30px;
  border: 1px solid;
  border-radius: 4px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
  font-weight: bold;
  transition: .3;
}

.button-default {
  color:        #fff !important;
  background:   #59c1b8;
  border-color: #59c1b8;
}

.button-default:hover {
  background:   #40a29a;
  border-color: #40a29a;
  text-decoration: none;
}

.button-inverted {
  color:        #59c1b8;
  background:   transparent;
  border-color: #59c1b8;
}

.button-inverted:hover {
  color:        #fff;
  background:   #59c1b8;
  border-color: #59c1b8;
  text-decoration: none;
}

.button-big {
  font-size: 20px;
  padding: 20px 70px;
}

.link {
  color: #59c1b8;
}

hr {
  margin-top: 30px;
  margin-bottom: 20px;
  border-top: 1px solid #e3e5e4;
}

/*********************
       Типография      
 *********************/

ul, ol {
   padding-left: 16px;
 }

/*********************
       TABS      
 *********************/

.tabs {
  margin: 40px 0 20px;
}

.tabs-ui {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
  cursor: pointer;
  border-bottom: 1px solid #e3e5e4;
}

.tab-ui {
  margin-right: 20px;
  font-size: 16px;
  padding-bottom: 5px;
  margin-bottom: -1px;
}

.tab-ui.active {
  border-bottom: 2px solid #59c1b8;
}

.tab-content__block {
  margin-bottom: 25px;
}

/* Header */

.header {}

/* Top Bar */

.header__top {
  height: 36px;
}

.header-controls {
  margin: 8px 0;
  padding: 0;
  list-style: none;
}

.header-controls-inner {
  float: right;
}

.header-controls__item {
  display: inline-block;
}

.header-controls__link {
  padding: 0 5px;
}

.header-controls__link,
.header-controls__separator {
  color: #59c1b8 !important;
}

/* Header Menu */

@media (min-width: 768px) {
  .header-collapse {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
}

.header-catalog__link {
  border-radius: 4px;

  color: #fff !important;
  background-color: #59c1b8 !important;
  padding: 10px 30px !important;

  width: 128px;
}

.header-catalog__link:hover {
  background-color: #40a29a !important;
}

.header-form {
  margin: 20px auto !important;
  padding: 0 !important;
  width: 100%;
}

.header-form-inner {
  position: relative;
  width: calc(100% - 40px) !important;
  margin: 0 20px;
}

.header-form__input {
  height: 40px;
  width: 100% !important;
}

.header-form__submit {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 60px;

  color: #fff !important;
  font-size: 20px;
  background-color: #59c1b8 !important;
  border: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  transition: .3s;
}

.header-form__submit:hover {
  background-color: #40a29a !important;
}

.header-cart {
  width: 56px;
  padding-top: 8px !important;
}

.header-cart__icon {
  font-size: 24px;
}

/* Footer */

.footer {
  background: #ccc;
}

.footer__copyright {
  margin: 0;
  padding: 20px 0;
  text-align: center;
}

.footer a {
  color: #59c1b8;
}

/*********************
       HOME PAGE      
 *********************/

/* Hero Block */

.hero {
  background-image: url(../images/hero-image.jpg);

  /* Create the parallax scrolling effect */
  /*background-attachment: fixed;*/
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  /*width: 100%;*/
  /*height: 480px;*/
  position: relative;
}

.hero__blackout {
  background-color: rgba(0, 0, 0, .25);

  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
}

.hero__content {
  color: #fff;

  width: 100%;
  height: 480px;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;

  position: relative;
  z-index: 200;
}

.hero__title {
  font-size: 42px;
  font-weight: 600;
  text-align: center;
  margin: 50px 0 25px;
}

.hero__subtitle {
  font-size: 24px;
  text-align: center;
  margin: 0;
}

/* Products List */

@media (min-width: 768px) {
  .products-list {
    margin: 80px;
  }
}

.products-list {
  margin: 80px;
}

.products-list__title {
  font-weight: 600;
  text-align: center;
  margin: 0 0 30px;
}

.products-list__more {
  margin-top: 30px;
  text-align: center;
}

.products-promo {
  background-color: #ddd7c7;
  background-image: url(../images/products-promo.png);
  background-repeat: no-repeat;
  background-position: center right;
  /*background-size: contain;*/
  border-radius: 5px;

  width: 100%;
  margin-bottom: 30px;
}

.products-promo__content {
  padding: 40px 60px;
}

.products-promo__title {
  font-size: 28px;
}

.product-item {
  margin-top: 15px;
  margin-bottom: 15px;
}

/*.products-list__inner .row:first-child .product-item:first-child {
  margin-top: 0;
}
.products-list__inner .row:last-child .product-item {
  margin-bottom: 0;
}*/

.product-item__link {
  display: block;
  border-radius: 5px;
  box-shadow: none;

  overflow: hidden;
  position: relative;
}

.product-item__link:hover {
  box-shadow: 0 2px 9px 0 rgba(0, 0, 0, .6);
  transition: box-shadow .3s .1s;
}

.product-item__image {

}

.product-item__info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  text-align: center;
  background-color: rgba(227, 228, 229, .85);
}

.product-item__link:hover .product-item__info {
  background-color: #efefef;
  transition: background-color .3s .1s;
}

.product-item__title {
  color: #000;
  font-weight: 600;
  font-size: 18px;
  display: inline-block;
  padding: 10px;
}

